import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, SociedadeUniProfissional } from '../../../../models/dtos/mlf/sociedadeUniProfissional';
import { INDICADOR_ESCOLARIDADE_PROF_ENUM_VALUES } from '../../../../models/enumerated/mlf/indicadorEscolaridadeProfEnum';
import { INDICADOR_HABILITACAO_PROF_ENUM_VALUES } from '../../../../models/enumerated/mlf/indicadorHabilitacaoProfEnum';
import { INDICADOR_PARTICIPACAO_SOCIETARIA_ENUM_VALUES } from '../../../../models/enumerated/mlf/indicadorParticipacaoSocietariaEnum';

interface SociedadeUniProfissionalEditViewProps extends EditViewProps<SociedadeUniProfissional> {}

const SociedadeUniProfissionalEditView: FC<SociedadeUniProfissionalEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<SociedadeUniProfissional>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 80;

  return (
    <WindowComponent title="Manutenção de Sociedade Uniprofissional - Empregados e Sócios">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 120 } }}
              id="nome"
              isPrependLabel={true}
              label="Nome"
              labelWidth={defaultWidth}
              placeholder="Informe o Nome"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="cpf"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="CPF"
              labelWidth={defaultWidth}
              placeholder="Informe o CPF"
              type={InputType.CPF}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="indicadorParticipacaoSocietaria"
              inputDropDownProps={{ optionsProvider: INDICADOR_PARTICIPACAO_SOCIETARIA_ENUM_VALUES }}
              isPrependLabel={true}
              label="Participação Societária"
              placeholder="Informe a Participação Societária"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="indicadorHabilitacao"
              inputDropDownProps={{ optionsProvider: INDICADOR_HABILITACAO_PROF_ENUM_VALUES }}
              isPrependLabel={true}
              label="Habilitação"
              placeholder="Informe a Habilitação"
              type={InputType.DROP_DOWN}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="indicadorEscolaridade"
              inputDropDownProps={{ optionsProvider: INDICADOR_ESCOLARIDADE_PROF_ENUM_VALUES }}
              isPrependLabel={true}
              label="Escolaridade"
              placeholder="Informe a Escolaridade"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default SociedadeUniProfissionalEditView;
